@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.social-links {
  width: 100%;
  position: absolute;
  margin: auto;
  bottom: 10px;
  text-align: center;
}

.social-links a img {
  width: 80px;
  height: 80px;
  padding: 25px;
  display: inline-block;
  margin: 5px 20px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.social-links a img:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.homepage-logo {
  max-width: 65%;
  max-height: 25%;
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.desc {
  max-width: 60%;
  max-height: 20%;
  position: absolute;
  margin: auto;
  left: 32%;
  right: 0;
  top: 50%;
  bottom: 0;
  font-weight: 2;
  font-size: 0.5rem;
  letter-spacing: 0.5rem;
  padding: 0;
  text-transform: uppercase;
}

@media screen and (max-width: 600px) {
  .social-links a img {
    margin: 0;
  }
}

* {
  box-sizing: border-box;
}

.main {
  max-width: 1200px;
  margin: 0 auto;
}

.container {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  flex-wrap: wrap;
}

.card {
  display: flex;
  padding: 1rem;
}

@media (min-width: 40rem) {
  .card {
    width: 50%;
  }
}
@media (min-width: 56rem) {
  .card {
    width: 33.3333%;
  }
}

.card-body {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 0.25rem;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  padding: 1rem;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.card-body:hover {
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.9);
}

.card-body h4 {
  font-size: 1.1rem;
  font-size: 700;
  letter-spacing: 1px;
  margin: 0px;
}

.card-body p {
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.5;
  margin-bottom: 1.25rem;
  color: rgb(70, 68, 68);
}

.card-body h6 {
  font-weight: 300;
  font-size: 0.9rem;
  color: rgb(37, 126, 153);
  margin-bottom: 1em;
}

.links a img {
  width: 30px;
  height: 30px;
  display: inline-grid;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.links a img:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.text {
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
  width: 35em;
}

a {
  color: rgb(0, 0, 0);
  padding: 10px;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

p span {
  display: block;
}

h2 span {
  display: block;
}

ul {
  margin: 25px;
  padding: 0;
}

li {
  padding: 5px;
}

@media screen and (max-width: 600px) {
  .text {
    margin: 15px;
  }
}

/* define the fonts and colors used in the project */
:root {
  --font: 'Open Sans', sans-serif;
  --color-bg: white;
  --color-txt: black;
  --color-home: rgb(240, 187, 237);
  --color-portfolio: rgb(189, 235, 188);
  --color-bio: rgb(255, 231, 125);
  --color-blog: rgb(175, 214, 240);
  --color-contact: rgb(220, 176, 238);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  font-family: 'Open Sans', sans-serif;
  font-family: var(--font);
  color: black;
  color: var(--color-txt);
  background: white;
  background: var(--color-bg);
}

.nav-bar {
  left: 0px;
  height: 40px;
  width: 100%;
  margin: 0 auto;
  margin-top: 25px;
}

.nav-bar ul {
  text-align: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav-bar ul li {
  display: inline-block;
  margin: 0 0.5rem;
  padding: 0;
  font-size: 0.7rem;
  text-transform: uppercase;
}

.nav-bar ul li a {
  color: inherit;
  padding: 0;
  letter-spacing: 0rem;
  position: relative;
  display: block;
  text-align: center;
  text-decoration: none;
}

.nav-bar ul li a:before {
  /* include a faux-border with a pseudo element  */
  content: '';
  position: absolute;
  /* by default have the faux-border hidden from view */
  top: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
}

.nav-bar ul li:nth-of-type(1) a:before {
  background: rgb(240, 187, 237);
  background: var(--color-home);
}
.nav-bar ul li:nth-of-type(2) a:before {
  background: rgb(189, 235, 188);
  background: var(--color-portfolio);
}
.nav-bar ul li:nth-of-type(3) a:before {
  background: rgb(255, 231, 125);
  background: var(--color-bio);
}
.nav-bar ul li:nth-of-type(4) a:before {
  background: rgb(175, 214, 240);
  background: var(--color-blog);
}
.nav-bar ul li:nth-of-type(5) a:before {
  background: rgb(220, 176, 238);
  background: var(--color-contact);
}

.nav-bar ul li a:hover:before {
  bottom: -4px;
}

.nav-bar .nav-pills:nth-of-type(1) .active {
  font-weight: bold;
}
.nav-bar .nav-pills:nth-of-type(2) .active {
  font-weight: bold;
}
.nav-bar .nav-pills:nth-of-type(3) .active {
  font-weight: bold;
}
.nav-bar .nav-pills:nth-of-type(4) .active {
  font-weight: bold;
}
.nav-bar .nav-pills:nth-of-type(5) .active {
  font-weight: bold;
}

