@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap');

* {
  box-sizing: border-box;
}

.main {
  max-width: 1200px;
  margin: 0 auto;
}

.container {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  flex-wrap: wrap;
}

.card {
  display: flex;
  padding: 1rem;
}

@media (min-width: 40rem) {
  .card {
    width: 50%;
  }
}
@media (min-width: 56rem) {
  .card {
    width: 33.3333%;
  }
}

.card-body {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 0.25rem;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  padding: 1rem;
  transition: all 0.4s ease-in-out;
}

.card-body:hover {
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.9);
}

.card-body h4 {
  font-size: 1.1rem;
  font-size: 700;
  letter-spacing: 1px;
  margin: 0px;
}

.card-body p {
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.5;
  margin-bottom: 1.25rem;
  color: rgb(70, 68, 68);
}

.card-body h6 {
  font-weight: 300;
  font-size: 0.9rem;
  color: rgb(37, 126, 153);
  margin-bottom: 1em;
}

.links a img {
  width: 30px;
  height: 30px;
  display: inline-grid;
  transition: all 0.2s ease-in-out;
}

.links a img:hover {
  transform: scale(1.5);
}
